import {
	refreshTokenFn
} from "@/api/login.js";
import api from "@/config/api.js";
import Request from "@/lib/request/index.js";
import Foundation from "@/utils/Foundation.js";
import {
	md5
} from "@/utils/md5.js";
import storage from "@/utils/storage.js";

import jwt from '@/js_sdk/t-jwt/jwt.js';
import uuid from "@/utils/uuid.modified.js";
import store from "../store";


let isNavigateTo = false

function cleanStorage() {
	uni.showToast({
		title: "",
		icon: "none",
		duration: 1500,
	});
	if (uni.showLoading()) {
		uni.hideLoading();
	}

	storage.setHasLogin(false);
	storage.setAccessToken("");
	storage.setRefreshToken("");
	console.log("token");
	storage.setUuid("");
	storage.setUserInfo({});


	if (!isNavigateTo) {
		isNavigateTo = true
		// 

		uni.navigateTo({
			url: "/pages/passport/wechatMPLogin",
		});






	}
}

let http = new Request();


/**
 * uuid
 */
const createUuid = () => {
	if (!storage.getUuid()) {
		storage.setUuid(uuid.v1());
		console.log("uuid", storage.getUuid());
	}
}


http.setConfig((config) => {
	createUuid();
	/*  */
	config.baseURL = api.buyer;
	config.header = {
		...config.header,
	};
	config.validateStatus = (statusCode) => {
		// ,
		return true;
	};
	return config;
});

http.interceptors.request.use(
	(config) => {
		/* async await  */
		let accessToken = storage.getAccessToken();
		if (accessToken) {
			/**
			 * JWT
			 * token
			 */
			const decodeJwt = jwt(accessToken);
			const timing = new Date().getTime() / 1000
			if (decodeJwt.exp <= timing) {
				accessToken = ""
				storage.setAccessToken('')
			}
			const nonce = Foundation.randomString(6);
			const timestamp = parseInt(new Date().getTime() / 1000);
			const sign = md5(nonce + timestamp + accessToken);
			const _params = {
				nonce,
				timestamp,
				sign,
			};
			let params = config.params || {};
			params = {
				...params,
				..._params
			};

			config.params = params;
			config.header.accessToken = accessToken;


		}
		createUuid();
		config.header = {
			...config.header,
			uuid: storage.getUuid()
		};
		return config;
	},
	(config) => {
		return Promise.reject(config);
	}
);


// 
let isRefreshing = false;
//
let requests = [];
// 
http.interceptors.response.use(
	async (response) => {
		isNavigateTo = false
		/* async await   */
		// tokentoken
		// if (isRefreshing && response.statusCode === 403) {
		//   cleanStorage();
		//   isRefreshing = false;
		// }
		uni.showLoading() ? uni.hideLoading() : ''
		let token = storage.getAccessToken();
		if (
			(token && response.statusCode === 403) ||
			response.data.status === 403
		) {
			if (!isRefreshing) {
				console.log('token', token)
				isRefreshing = true;
				storage.setAccessToken('')
				let oldRefreshToken = storage.getRefreshToken();
				//token
				return refreshTokenFn(oldRefreshToken)
					.then((res) => {
						let {
							accessToken,
							refreshToken
						} = res.data.result;
						storage.setAccessToken(accessToken);
						storage.setRefreshToken(refreshToken);

						response.header.accessToken = `${accessToken}`;
						// token 
						console.log('', requests, 'token', accessToken)
						requests.forEach((cb) => cb(accessToken));
						requests = []; // 
						return http.request(response.config);
					})
					.catch((err) => {
						console.log('token' + oldRefreshToken, err)
						cleanStorage();
						return Promise.reject(err);
					})
					.finally(() => {
						isRefreshing = false;
					});
			} else {
				//  resolve  Promise
				return new Promise((resolve) => {
					//  resolve 
					requests.push((token) => {
						response.header.accessToken = `${token}`;
						resolve(http.request(response.config));
					});
				});
			}

			// 
		} else if (
			(!token && !storage.getRefreshToken() && response.statusCode === 403) ||
			response.data.code === 403
		) {
			console.log('token token ', token, storage.getRefreshToken())
			cleanStorage();

			// success
		} else if (
			(response.statusCode == 200 && !response.data.success) ||
			response.statusCode == 400
		) {
			if (response.data.message) {
				uni.showToast({
					title: response.data.message,
					icon: "none",
					duration: 1500,
					success: function () {
						store.state.isShowToast = true;
					},
					fail: function () {
						store.state.isShowToast = false;
					},
					complete: function () {
						store.state.isShowToast = false;
					}
				});
			}
		}
		return response;
	},
	(error) => {
		return error;
	}
);

export {
	http
};

export const Method = {
	GET: "GET",
	POST: "POST",
	PUT: "PUT",
	DELETE: "DELETE",
};
