import config from '@/config/config.js'
import storage from '@/utils/storage';
class socketIO {
  constructor(data, time, url) {
    this.socketTask = null
    this.is_open_socket = false //
    this.url = config.baseWsUrl + '/' + storage.getAccessToken()  //
    this.data = data ? data : null
    this.connectNum = 1 // 
    this.traderDetailIndex = 100 // traderDetailIndex ==2 
    this.accountStateIndex = 100 // traderDetailIndex ==1 
    this.followFlake = false // traderDetailIndex == true 
    //
    this.timeout = time ? time : 15000 //
    this.heartbeatInterval = null //
    this.reconnectTimeOut = null //
  }
  // websocket
  connectSocketInit (data) {
    this.data = data
    this.socketTask = uni.connectSocket({
      url: this.url,
      success: () => {
        console.log("websocket...");
        // 
        return this.socketTask
      },
    });
    this.socketTask.onOpen((res) => {
      this.connectNum = 1
      console.log("WebSocket");
      this.send(data)
      clearInterval(this.reconnectTimeOut)
      clearInterval(this.heartbeatInterval)
      this.is_open_socket = true;
      this.start();
      //  
      this.socketTask.onMessage((e) => {
        // json
        let res = JSON.parse(e.data);
        console.log("res---------->", res) //   
        if (res.data) {
          uni.$emit('getPositonsOrder', res);
        }
      });
    })
    // onclose
    uni.onSocketError((res) => {
      console.log('WebSocket');
      this.socketTask = null
      this.is_open_socket = false;
      clearInterval(this.heartbeatInterval)
      clearInterval(this.reconnectTimeOut)
      uni.$off('getPositonsOrder')
      if (this.connectNum < 6) {
        uni.showToast({
          title: `WebSocket${this.connectNum}`,
          icon: "none"
        })
        this.reconnect();
        this.connectNum += 1
      } else {
        uni.$emit('connectError');
        this.connectNum = 1
      }
    });
    // socket
    this.socketTask.onClose(() => {
      console.log("-------")
      clearInterval(this.heartbeatInterval)
      clearInterval(this.reconnectTimeOut)
      this.is_open_socket = false;
      this.socketTask = null
      uni.$off('getPositonsOrder')
      if (this.connectNum < 6) {
        this.reconnect();
      } else {
        uni.$emit('connectError');
        this.connectNum = 1
      }
    })
  }
  // socket
  Close () {
    if (!this.is_open_socket) {
      return
    }
    this.socketTask.close({
      success () {
        uni.showToast({
          title: 'SocketTask ',
          icon: "none"
        });
      }
    });
  }
  //
  send (data) {
    console.log("data---------->", data);
    //  
    if (this.socketTask) {
      this.socketTask.send({
        data: JSON.stringify(data),
        async success () {
          console.log("");
        },
      });
    }
  }
  //
  start () {
    this.heartbeatInterval = setInterval(() => {
      this.send({
        "traderid": 10260,
        "type": "Ping"
      });
    }, this.timeout)
  }
  //
  reconnect () {
    //
    clearInterval(this.heartbeatInterval)
    //
    if (!this.is_open_socket && (this.traderDetailIndex == 2 || this.accountStateIndex == 0 || this
      .followFlake)) {
      this.reconnectTimeOut = setInterval(() => {
        this.connectSocketInit(this.data);
      }, 5000)
    }
  }
  /**
   * @description  scoket 
   * @param {array} array
   * @param {string} type   openposition 
   */
  arrayFilter (array, type = 'normal', signalId = 0) {
    let arr1 = []
    let arr2 = []
    let obj = {
      arr1: [],
      arr2: []
    }
    arr1 = array.filter(v => v.flwsig == true)
    arr2 = array.filter(v => v.flwsig == false)
    if (type == 'normal') {
      if (signalId) {
        arr1 = array.filter(v => v.flwsig == true && v.sigtraderid == signalId)
        return arr1
      } else {
        return arr1.concat(arr2)
      }
    } else {
      if (signalId > 0) {
        arr1 = array.filter(v => v.flwsig == true && v.sigtraderid == signalId)
        obj.arr1 = arr1
      } else {
        obj.arr1 = arr1
      }
      obj.arr2 = arr2
      return obj
    }
  }
}
export {
  socketIO
}