/**
 * 
 * @param val  auth
 *  auth 
 */
export function isLogin (val) {
  let userInfo = storage.getUserInfo();
  if (val == "auth") {
    return userInfo && userInfo.id ? true : false;
  } else {
    return storage.getUserInfo();
  }
}

/**
 * 
 *
 */
export function quiteLoginOut () {
  uni.showModal({
    title: "",
    content: "",
    confirmColor: Vue.prototype.$mainColor,
    async success (res) {
      if (res.confirm) {
        storage.setAccessToken("");
        storage.setRefreshToken("");
        storage.setUserInfo({});
        storage.setHasLogin(false)
        navigateToLogin("redirectTo");
        await logout();
      }
    },
  });
}

/**
 * 
 *
 */
export function logoff () {
  uni.showModal({
    title: "",
    content: "6",
    confirmColor: Vue.prototype.$mainColor,
    async success (res) {
      if (res.confirm) {
        await logoffConfirm();
        storage.setAccessToken("");
        storage.setRefreshToken("");
        storage.setUserInfo({});
        navigateToLogin("redirectTo");
      }
    },
  });
}


/**
 * im
 */
export function talkIm (storeId, goodsId, id) {
  if (isLogin('auth')) {
    let url = `/pages/mine/im/index?userId=${storeId}`
    if(goodsId && id) url = `/pages/mine/im/index?userId=${storeId}&goodsid=${goodsId}&skuid=${id}`
    uni.navigateTo({
      url
    });
  }
  else {
    tipsToLogin()
  }
}

export function tipsToLogin (type) {
  if (!isLogin("auth")) {
    uni.showModal({
      title: "",
      content: "",
      confirmText: "",
      cancelText: "",
      confirmColor: Vue.prototype.$mainColor,
      success: (res) => {
        if (res.confirm) {
          navigateToLogin();
        } else if (res.cancel) {
          if(type !== 'normal'){
            uni.navigateBack();
          }
          
        }
      },
    });
    return false;
  }
  return true;
}

