/**
 * API
 */

import { http, Method } from "@/utils/request.js";




/**
 * 
 * @param params
 */
 export function getReceipt(params) {
  return http.request({
    url: "/trade/carts/select/receipt",
    method: Method.GET,
    needToken: true,
    params,
  });
}

/**
 * 
 * @param id
 */
export function getReceiptDetail(id) {
  return http.request({
    url: `/trade/receipt/${id}`,
    method: Method.GET,
    needToken: true,
  });
}

/**
 * 
 * @param params
 */
export function selectedShipMethod(params) {
  return http.request({
    url: "/trade/carts/shippingMethod",
    method: Method.GET,
    needToken: true,
    params,
  });
}

/**
 * 
 * @param params
 */
export function getOrderList(params) {
  return http.request({
    url: "/order/order",
    method: Method.GET,
    needToken: true,
    params,
  });
}

/**
 * 
 * @param orderSn 
 */
export function getOrderDetail(orderSn) {
  return http.request({
    url: `/order/order/${orderSn}`,
    method: Method.GET,
    needToken: true,
  });
}

/**
 * 
 * @param orderSn 
 * @param reason   
 */
export function cancelOrder(orderSn, reason) {
  return http.request({
    url: `/order/order/${orderSn}/cancel`,
    method: Method.POST,
    needToken: true,
    header: { "content-type": "application/x-www-form-urlencoded" },
    data: reason,
  });
}

/**
 * 
 * @param orderSn 
 */
export function confirmReceipt(orderSn) {
  return http.request({
    url: `/order/order/${orderSn}/receiving`,
    method: Method.POST,
    needToken: true,
  });
}



/**
 * 
 * @param {*} parentOrderSn
 * @param {*} skuId
 */
export function getPinTuanShare(parentOrderSn,skuId) {
  return http.request({
    url: `promotion/pintuan/share`,
    method: Method.GET,
    needToken: true,
    params:{parentOrderSn,skuId}
  });
}

