/**
 * API
 */

import { http, Method } from "@/utils/request.js";

/**
 * 
 * @param show_type  all checked
 */
export function getCarts() {
  return http.request({
    url: `/trade/carts/all`,
    method: Method.GET,
    needToken: true,
    loading: false,
  });
}

/**
 * 
 * @param show_type  all checked
 */
export function getCartNum() {
  return http.request({
    url: `/trade/carts/count`,
    method: Method.GET,
    needToken: true,
    loading: false,
  });
}

/**
 * 
 * @param way CART/BUY_NOW/PINTUAN / POINT
 */
export function getCartCouponNum(way) {
  return http.request({
    url: `/trade/carts/coupon/num?way=${way}`,
    method: Method.GET,
    needToken: true,
    loading: false,
  });
}

/**
 * 
 * @param skuId      ID
 * @param num         
 * @param cartType   	
 */
export function addToCart(data) {
  return http.request({
    url: "/trade/carts",
    method: Method.POST,
    needToken: true,
    header: { "content-type": "application/x-www-form-urlencoded" },
    data,
  });
}



/**
 * 
 * @param skuId
 * @param num
 */
export function updateSkuNum(skuId, num = 1) {
  return http.request({
    url: `/trade/carts/sku/num/${skuId}`,
    method: Method.POST,
    header: { "content-type": "application/x-www-form-urlencoded" },
    needToken: true,
    data: { num },
  });
}

/**
 * 
 * @param skuId
 * @param checked
 */
export function updateSkuChecked(skuId, checked) {
  return http.request({
    url: `/trade/carts/sku/checked/${skuId}`,
    method: Method.POST,
    needToken: true,
    header: { "content-type": "application/x-www-form-urlencoded" },
    data: { checked },
  });
}

/**
 * 
 * @param skuIds
 */
export function deleteSkuItem(skuIds) {
  return http.request({
    url: `/trade/carts/sku/remove?skuIds=${skuIds}`,
    method: Method.DELETE,
    needToken: true,
  });
}


/**
 * 
 * @param checked
 */
export function checkAll(checked) {
  return http.request({
    url: "/trade/carts/sku/checked",
    method: Method.POST,
    needToken: true,
    params: { checked },
  });
}

/**
 * 
 * @param storeId
 * @param checked
 */
export function checkStore(storeId, checked) {
  return http.request({
    url: `/trade/carts/store/${storeId}`,
    method: Method.POST,
    needToken: true,
    header: { "content-type": "application/x-www-form-urlencoded" },
    data: { checked },
  });
}

/**
 * 
 */
export function getCheckoutParams(way) {
  return http.request({
    url: "/trade/carts/checked?way=" + way,
    method: Method.GET,
    needToken: true,
  });
}

/**
 * ID
 * @param addressId
 */
export function setAddressId(addressId,way) {
  return http.request({
    url: `/trade/carts/shippingAddress?shippingAddressId=${addressId}&way=${way}`,
    method: Method.GET,
    needToken: true,
  
  });
}
/**
 * ID
 * @param addressId
 */
 export function setStoreAddressId(storeAddressId,way) {
  return http.request({
    url: `/trade/carts/storeAddress?storeAddressId=${storeAddressId}&way=${way}`,
    method: Method.GET,
    needToken: true,
  
  });
}


/**
 * 
 */
export function createTrade(params) {
  return http.request({
    url: "/trade/carts/create/trade",
    method: Method.POST,
    needToken: true,
    message: false,
    data:params,
  });
}


/**
 * 
 * @param params
 */
export function getCashierData(params) {
  return http.request({
    url: "payment/cashier/tradeDetail",
    method: Method.GET,
    needToken: true,
    params,
  });
}


/**
 * 
 * @param paymentMethod
 * @param paymentClient
 * @param params
 * @returns {*|*}
 */
export function initiatePay(paymentMethod, paymentClient, params) {
  return http.request({
    url: `payment/cashier/pay/${paymentMethod}/${paymentClient}`,
    method: Method.GET,
    needToken: true,
    params,
  });
}
  

