/**
 * @param appType
 * @returns {AxiosPromise}
 * 
 */	
 export function getAppVersion(appType) {
  return http.request({
    url: `/other/appVersion/${appType}`,
    method: Method.GET,
    type:"manager"
  });
}

/**
 * @param appType
 * @returns {AxiosPromise}
 * 
 */	
 export function getAppVersionList(type,data) {
  return http.request({
    url: `/other/appVersion/appVersion/${type}`,
    method: Method.GET,
    type:"manager",
    data
  });
}
